.section-intro{
    width: 100%;
    height: 750px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #161616;


    .main{
        width: 80%;
        height: 550px;
        display: flex;

        .intro-text{
            width: 60%;
            p{
                font-family: 'Roboto Mono', monospace;
                color: #4BFFA5;
            }

            h1{
                font-family: 'Space Grotesk', sans-serif;
                font-size: 60px;
                color: #F6F6F6;
                text-transform: uppercase;
            }
            .btn{
                position: relative;
                display: block;
                overflow: hidden;
                width: 151px;
                height: 46px;
                border: 2px solid #4BFFA5;
                background-color: transparent;
                font-family: 'Roboto Mono', monospace;
                color:#4BFFA5 ;
                margin-top: 40px;
                cursor: none;
                transition: all 500ms ease;
                z-index: 0;

                &::before{
                    content: "";
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 0;
                    margin: auto;
                    background-color: #4BFFA5;
                    transition: all 500ms ease;
                    z-index: -1;
                }


                &--1::before{
                    width: 0%;
                    height: 100%;
                }
                &--1:hover{
                    color: rgb(10, 10, 10);
                     &::before{
                         width: 100%;
                     }
                }
            
            }
            



        }

        .intro-img{
            width: 40%;
            height: 470px;
            display: flex;
            justify-content: center;
            align-items: center;
            

            img{
                max-width: 100%;
                height: auto;               
                width: 370px;
                // height: 500px; 
                box-shadow:  rgb(22, 22, 22) 15px 20px 0px -3px, rgb(75, 255, 165) 15px 20px ;           
                transition-duration: 1s ;
                &:hover{
                    transform: scale(1.03);
                    transition: 1s;
                }
            }

            
            
        }
    }
}

