/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    .footer{
        width: 100%;
        height: 150px;
        background-color: #161616;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items:center;
        
    
        .info-1{
            display: flex;
            align-items: center;
            justify-content: center;
            width: 80%;
            height: 30px;
    
            p{
                display: none;
                width: 90px;
                margin-top: 12px;
                color: #F6F6F6;
                font-family: 'Space Grotesk', sans-serif;
            }
            .line{
                display: none;
                width: 46px;
                height: 0px;
                border: 1px solid;
                color: #F6F6F6;          
            }
    
            .icons{
                margin-left: 10px;
                width: 170px;
                display: flex;
                align-items: center;
                justify-content: space-evenly;
    
    
                .social{
                    font-size: 18px;
                    color: #F6F6F6;
                }
            }
        }
    
    
        .info-2{
            width: 81%;
