html,
body,
*{
  cursor: none;
}


.loader{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #000000;

  img{
    width: 550px;
    height: auto;
  }

}

.main-page{
    overflow-x: hidden;


}


::-webkit-scrollbar {
    width: 15px;
  }


::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 0px;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: grey; 
    border-radius: 0px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background-color: rgb(88, 88, 88); 
  }