.section-about{
    width: 100%;
    height: 700px;
    display: flex;
    background-color: #161616;



    .about{
        display: flex;
        justify-content: space-around;

        .number{
            h1{
                font-family: 'Roboto Mono', monospace;
                color:#4BFFA5 ;
            }
        }
        .text{
            width: 45%;
            margin-left: 70px;
            
            h1{
                color: #fff;
                font-family: 'Roboto Mono', monospace;
            }

            span{
                display: flex;
                width: 490px;
                justify-content: flex-start;
                .icon{
                    width: 1rem;
                    color: #4BFFA5;
                    margin-top: 60px;
                }
                p{
                    width: 71%;
                    margin-top: 55px;
                    margin-left: 17px;
                    color: #989898;                   
                    font-family: 'Space Grotesk', sans-serif;
                    line-height: 1.6;                             
                }
            }
            
            .btn{
                position: relative;
                display: block;
                overflow: hidden;
                width: 97px;
                height: 46px;
                border: 2px solid #4BFFA5;
                font-family:'Roboto Mono', monospace ;
                background-color: transparent;
                color: #4BFFA5;
                margin-top: 70px;
                cursor: none;
                transition: all 500ms ease;
                z-index: 0;

                &::before{
                    content: "";
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 0;
                    margin: auto;
                    background-color: #4BFFA5;
                    transition: all 500ms ease;
                    z-index: -1;
                }


                &--1::before{
                    width: 0%;
                    height: 100%;
                }
                &--1:hover{
                    color: rgb(10, 10, 10);
                     &::before{
                         width: 100%;
                     }
                }
            }
        }

        .picture{
            width: 400px;
            height: 400px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 70px;
            img{
                max-width: 100%;
                height: auto;
            }
        }
    }
}

